import Vue from 'vue';
import routes from './routes';
const { request } = routes;

const state = {
    info: {
        storage_price_ask: 7680
    },
    value: {
        host_stats: {
            online: false,
            uptime: 0,
            score: 0,
            storage_used: 0,
            storage_cap: 0,
            storage_disk_total:0,
            storage_disk_available:0,
        },
        renter_stats: {

        }
    },
    contracts: {
        "active_contract_num":0,
        "compensation_paid":0,
        "compensation_outstanding":0,
        "first_contract_start":"0001-01-01T00:00:00Z",
        "last_contract_end":"0001-01-01T00:00:00Z",
        "role":0
    },
    contractsList: {
        "contracts":[]
    },
    setup: {
        path: null,
        size: null
    },
    isSynced: false,
    isImporting: false,
    proposedStorageAvailable: null

};
const mutations = {
    setInfo(s, stats) {
        Vue.set(s, 'info', stats);
    },
    setStatsInfo(s, stats) {
        Vue.set(s, 'value', stats);
    },
    setContractsInfo(s, stats) {
        let formattedStats = stats
        formattedStats.compensation_outstanding = formattedStats.compensation_outstanding/1000000
        formattedStats.compensation_paid = formattedStats.compensation_paid/1000000
        Vue.set(s, 'contracts', formattedStats);
    },
    setContractsList(s, stats) {
        Vue.set(s, 'contractsList', stats);
    },
    setStorageUsedTest(s, gb) {
        let b =  gb*Math.pow(1000, 3)
        Vue.set(s.value.host_stats, 'storage_cap', b);
    },
    setSetupValues(s, options) {
        Vue.set(s, 'setup', {...s.setup, ...options})
    },
    setIsSynced(s, val) {
        Vue.set(s, 'isSynced', val)
    },
    setIsImporting(s, val) {
        Vue.set(s, 'isImporting', val)
    },
    setProposedStorageAvailable(s, val) {
        Vue.set(s, 'proposedStorageAvailable', val)
    }
};
const getters = {
    statsInfo(s) {
        return s.value;
    },
    contractInfo(s) {
        return s.value;
    }
};
const actions = {
    storageInfo(context) {
        return request({
            endpoint: 'v1/storage/info',
            method: 'POST',
        }).then((stats) => {
            // console.log('storage/info\n', JSON.parse(stats))
            let data = JSON.parse(stats)

            //convert btt/day/GB to btt/month/TB
            let price = Math.round(data.storage_price_ask/1000000*30*1024)

            data.storage_price_ask = price
            context.commit('setInfo', data);
        });
    },
    storageStatsInfo(context, local) {
        let reqObj = {
            endpoint: 'v1/storage/stats/info',
            method: 'POST',
        }
            reqObj.params = {
                'l': local
            }
        return request(reqObj)
        .then((stats) => {
            let data = JSON.parse(stats)
            // console.log('storage/stats/info\n', data)
            if(data.host_stats){
                context.commit('setStatsInfo', data)
            }
        });
    },
    storageStatsSync() {
        return request({
            endpoint: 'v1/storage/stats/sync',
            method: 'POST',
        }).
        then((res) => {
            //return sync success or fail
            //empty response is a success
            return (res === "") ? true : false 

        })
    },
    storageAnnounce(context, options) {
        return request({
            endpoint: 'v1/storage/announce',
            method: 'POST',
            params: {
                'host-storage-price': null,
                'host-storage-max': options['hostStorageMax']
            }
        }).then(() => {
            // console.log('announcing ->', options['hostStorageMax'], options['hostStorageMax']*Math.pow(1000, 3))
            return context.dispatch('storageStatsInfo', options.local)
        })
    },
    storageAnnouncePrice(context, options) {
        let price = Math.round(options['hostStoragePrice']*1000000/30/1024)
        return request({
            endpoint: 'v1/storage/announce',
            method: 'POST',
            params: {
                'host-storage-price': price,
                'host-storage-max': null
            }
        }).then(() => {
            return context.dispatch('storageInfo')
        })
    },
    storageContractsInfo(context) {
        return request({
            endpoint: 'v1/storage/contracts/stat',
            method: 'POST',
            params: {
                role: 'host'
            }
        }).then((stats) => {
            // console.log('storage/contract/stat\n', JSON.parse(stats))
            context.commit('setContractsInfo', JSON.parse(stats));
        });
    },
    storageContractsSync() {
        return request({
            endpoint: 'v1/storage/contracts/sync',
            method: 'POST',
            params: {
                role: 'host'
            }
        })
        .then((res) => {
            //return sync success or fail
            //empty response is a success
            return (res === "") ? true : false 
        })
    },
    storageContractsList(context) {
        return request({
            endpoint: 'v1/storage/contracts/list',
            method: 'POST',
            params: {
                role: 'host',
                order:"escrow_time,asc",
                status:"active",
                size:20,
            }
        }).then((stats) => {
            context.commit('setContractsList', JSON.parse(stats));
        });
    },
    appReset() {

        return new Promise((resolve) => {

            let interval = setInterval(() => {

                request({
                    endpoint: 'v1/storage/path/status',
                    method: 'POST',

                })
                .then(() => {
                    clearInterval(interval)
                    return resolve()
                })
                .catch(() => {
                })

            }, 2000)
        })

    },
    storagePath(context, options) {
        if(options.suppressNotification !== true){
            context.dispatch('notificationInProgress', 'host')
        }
        else{
            context.commit('setIsImporting', true)
        }
        return request({
            endpoint: 'v1/storage/path',
            method: 'POST',
            params: {
                arg: `${options.path}&arg=${options.size}`
            }
        })
        .then(() => {
            return handleReset(context, options.suppressNotification)
        })
        .catch(() => {
            // hack to handle btfs empty response
            // btfs should return 200 before it resets
            return handleReset(context, options.suppressNotification)
        })

        function handleReset(context, suppressNotification) {
            return context.dispatch('appReset')
            .then(() => {
                context.dispatch('metaRepoStat')

                if(suppressNotification !== true){
                    context.dispatch('notificationComplete', 'host')
                }
                else{
                    context.commit('setIsImporting', false)
                }
            })
        }
    },
    storagePathCapacity(context, options) {
        return request({
            endpoint: 'v1/storage/path/capacity',
            method: 'POST',
            params: {
                arg: options.path
            }
        })
        .then((stats) => {
            let data = JSON.parse(stats)
            let freeSpaceGB = bToGB(data.FreeSpace)
            if(data.Type === "error") {
                //{"Message":"no such file or directory","Code":0,"Type":"error"}
                return Promise.reject(data.Message)

            }
            else if(freeSpaceGB < 32 || !data.Valid ) {
                // if not valid or .btfs dir already exists
                let errorMessage = (data.Valid) ? 'You do not have enough space on your hard drive' : `${options.path.replace(/\/+$/, '')}/.btfs is not empty`
                return Promise.reject(errorMessage)

            }

            context.dispatch('updateProposedStorageAvailable', data.FreeSpace)
            return Promise.resolve('[storagePathCapacity] pass')
        })
        .catch((e) => {
            return Promise.reject(e)
        })
    },
    updateHostSetupValues(context, options) {
        context.commit('setSetupValues', options)
    },
    updateIsSynced(context, val) {
        context.commit('setIsSynced', val)
    },
    updateProposedStorageAvailable(context, val) {
        context.commit('setProposedStorageAvailable', val)
    }
};

function bToGB(b) {
  let gb = b/Math.pow(1000, 3)
  gb = Math.floor(gb)
  return gb
}

export default {
    state,
    getters,
    mutations,
    actions,
};