<script>
import Button from '../components/Button.vue'
import InputBox from '../components/InputBox.vue'
import colors from '../scss/_theme.scss';

export default {
	name: 'EditHostFolder',
	components: {
		Button,
		InputBox
	},
	props: {
		hostDirectoryPath: {
			type: String,
			default: null
		},
		storageLocationContinueMessage: {
			type: String,
			default: "Files will be moved to this folder after you click on ‘Save changes’"
		},
		checkOtherFolder: {
			type: Function,
			default: () => {}
		}
	},
    data() {
        return {
			newHostDirectoryPath: this.hostDirectoryPath,
			colors,
			isOpen: false,
			storageLocationErrorMessage: '',
			isStorageLocationInvalid: false,
			currentHostDirectoryPath: this.hostDirectoryPath,
        }
    },
	methods: {
		toggleOpen() {
			this.newHostDirectoryPath = this.currentHostDirectoryPath
			this.isOpen = !this.isOpen
			this.$emit('isEditingFolder', this.isOpen)
		},
		handleUpdate() {
			this.checkOtherFolder({path: this.newHostDirectoryPath})
			.then(() => {
				this.$emit('inputChange', this.newHostDirectoryPath)
				this.currentHostDirectoryPath = this.newHostDirectoryPath
				this.toggleOpen()
			})
			.catch((e) => {
				console.log(e)
				this.isStorageLocationInvalid = true
				this.storageLocationErrorMessage = e
			})
		},

	},
	computed: {
		storageLocationMessage() {
			if(this.isStorageLocationInvalid) {
				return this.storageLocationErrorMessage
			}
			else if (!this.isStorageLocationDifferent || this.newHostDirectoryPath === "") {
				return 'Please manually input your new Host file path'
			}
			else{
				return this.storageLocationContinueMessage
			}
		},
		isStorageLocationDifferent() {
			return this.hostDirectoryPath !== this.newHostDirectoryPath
		},
	},
	watch:{
		newHostDirectoryPath() {
			this.isStorageLocationInvalid = false
		}
	}
};
</script>

<template>
	<div class="edit-host-folder">
		<div class="current">
			<div class="">
				<p class="subtitle">
					Current host folder destination
				</p>
				<b>
					{{currentHostDirectoryPath}}
				</b>
			</div>
			<div>
				<Button type="outline" :isDisabled="isOpen" :cb="toggleOpen"> 
					<div class="edit-button">
						Edit 
						<svg-icon icon="edit" :hasFill="true" class="icon"/>
					</div>
				</Button>
			</div>
		</div>
		<div class="edit" v-if="isOpen">
			<p class="subtitle">
				Edit host folder destination (case-sensitive)
			</p>
			<div class="input-container">
				<InputBox 
				v-model="newHostDirectoryPath" 
				inputType="text" 
				:borderColor="colors.btfsBlue500"
				:placeholderColor="colors.gray500"
				:invalidColor="colors.btfsRed"
				:isInvalid="isStorageLocationInvalid"
				:placeholder="hostDirectoryPath"
				:message="storageLocationMessage"
				/>
			</div>
			<div class="button-container">
				<Button type="outline" size="small" class="cancel" :cb="toggleOpen"> 
					Cancel
				</Button>
				<Button type="primary" size="small" :cb="handleUpdate" :isDisabled="!isStorageLocationDifferent"> 
					Update
				</Button>
			</div>

		</div>
	</div>
</template>

