<script>
import Button from '../components/Button.vue'
import InputBox from '../components/InputBox.vue'
import colors from '../scss/_theme.scss';
import StepIndicator from '../components/StepIndicator.vue'
import PasswordCreate from '../components/PasswordCreate.vue'
import Popup from '../components/Popup.vue';

export default {
	name: 'WalletImport',
	props: {
		setPassword: {
			type: Function,
			default: () => {}
		},
		importWallet: {
			type: Function,
			default: () => {}
		},
		setInitializationFlag: {
			type: Function,
			default: () => {}
		},
	},
	components: {
		Button,
		InputBox,
		StepIndicator,
		PasswordCreate,
		Popup
	},
	methods: {
		handleNavClick(navItem) {
			this.selectedNavItem = navItem
			this.input = null
		},
		nextStep() {
			this.current += 1
		},
		handlePopoutClose() {
            this.showPopup = false
            this.popupState = 0
        },
        handleImportWallet() {
			this.showPopup = true
			let type = (this.selectedNavItem === "private key") ? 'p' : 'm'
			this.importWallet({type: type, value: this.input})
			.then(() => {
				this.nextStep()
				this.handlePopoutClose()
			})
			.catch((e) => {
				console.log(e)
				if(this.selectedNavItem === "private key") {
					this.handlePopoutClose()
					this.invalidPrivateKey = true
					//change messaging
				}
				else{
					this.popupState = 1
				}
			})
		},
		handleSetPassword() {
			this.setPassword({password: this.password})
			.then(() => {
				return this.setInitializationFlag()
			})
			.then(() => {
				this.$router.push('/wallet')
			})

		},
        handleContinue() {
			switch(this.current){
				case 1:
					this.handleImportWallet()
					break;
				case 2:
					this.handleSetPassword()
					break;

			}
        }
	},
	data() {
		return {
			colors,
			navList: [
				'recovery phrase',
				'private key'
			],
			selectedNavItem: 'recovery phrase',
			steps: ['1', '2'],
			current: 1,
			showPopup: false,
			popupState: 0,
			input: null,
			password: '',
			passwordsMatch: false,
			invalidPrivateKey: false
		}
	},
	computed: {
		isValidInput() {
			if(this.selectedNavItem === 'recovery phrase') {
				if(this.input === null) return false
				let mnemonicArr = this.input.trim().split(' ')

				return mnemonicArr.length === 12
			}
			else {

				if(this.input === null) {
					return false
				}
				if(this.invalidPrivateKey === true) {
					return false
				}
				
				return this.input.length === 48 || this.input.length === 64
			}
		},
		isInputEmpty() {
			return this.input === null
		},
		inputMessage() {
			if(this.isInputEmpty || this.isValidInput) {
				return ''
			}
			else if(this.selectedNavItem === 'private key' && this.invalidPrivateKey) {
				return 'The recovery phrase sequence is incorrect.'
			}
			else {
				return (this.selectedNavItem === 'recovery phrase') ? 'The recovery phrase must be 12 words' : 'The private key must be 48 or 64 characters'
			}
		},
		canContinue() {
			return (this.current === 1) ? this.isValidInput : this.passwordsMatch
		}
	},
	watch: {
		input() {
			this.invalidPrivateKey = false
		}
	} 
}
</script>

<template>
	<div class="wallet-import">
		<StepIndicator class="step-container" :steps="steps" :current="current"></StepIndicator>
		<div v-if="current === 1" class="container">
			<div class="header-container">
				<p class="header">Import Wallet</p>
				<p class="description">
					You are about to input highly sensitive information, please DO NOT expose to strangers. 
				</p>
			</div>

			<div class="nav">
				<div
					v-for="navItem in navList" 
					:key="navItem"
					class="nav-item"
					:class="{'nav-item-selected': navItem === selectedNavItem}"
					@click="()=> {handleNavClick(navItem)}"
				>
					{{ navItem }}
				</div>
			</div>

			<div class="text-container">
				<InputBox 
				v-model="input"
				inputType="textarea" 
				:borderColor="colors.btfsBlue500"
				:placeholderColor="colors.gray800"
				:invalidColor="colors.btfsRed"
				:placeholder="(this.selectedNavItem === 'recovery phrase') ? '12 words in order separated by a single space (ex: word word word...)' : ''"
				:delayEmit="true"
				:isInvalid="!isValidInput && !isInputEmpty"
				:message="inputMessage"
				:showMessage="true"
				/>
			</div>

		</div>

		<div v-if="current === 2" class="container">
			<div class="header-container">
				<p class="header">Wallet password</p>
				<p class="description">
					Create a strong password and save it in a safe place. We do not have access to your password and will not be able to restore it. Losing your password may result in losing access to your funds. 
				</p>
			</div>
			<PasswordCreate @passwordsMatch="(val)=>{passwordsMatch = val}" @password="(val) => {password = val}"/>

		</div>

		<div class="text-container">
			<p class="text-sm">
				By clicking “Continue” you agree to the <a href="https://www.bittorrent.com/legal/terms-of-use/" target="_blank" rel="noreferrer noopener">Terms of Service</a> and <a href="https://www.bittorrent.com/legal/privacy-policy/" target="_blank" rel="noreferrer noopener">Privacy Policy</a>.
			</p>
		</div>


		<div class="button-container">
			<router-link to="/wallet/setup">
				<Button type="text">Create instead</Button>
			</router-link>
			<Button :cb="handleContinue" :isDisabled="!canContinue">Continue</Button>
		</div>

		<Popup :isOpen="showPopup" @onClose="handlePopoutClose">
			<div class="text-center" v-if="this.popupState === 0">
				<p class="popup-header">Importing...</p>
				<p class="popup-description">This transfer may take up to a minute.</p>
				<img src="../assets/svg/spinner.svg" class="spinner-icon"/>
			</div>
			<div class="text-center" v-if="this.popupState === 1">
				<p class="popup-header">System Error</p>
				<p class="popup-description">Please try again later</p>
				<div class="button-container center">
					<Button :cb="handlePopoutClose">Continue</Button>
				</div>
			</div>
		</Popup>
	</div>
</template>

