<script>
import Popup from './Popup'
import InputBox from './InputBox'
import Button from './Button'
import colors from '../scss/_theme.scss';

export default {
	name: 'RecoveryInformation',
	components: {
		Popup,
		InputBox,
		Button
	},
	props: {
		close: {
			type: Function,
			deafult: () => {}
		},
		checkPassword: {
			type: Function,
			default: () => {}
		},
	},
    data() {
        return {
			state:0,
			colors,
			password: null,
			passwordInvalid: false,
			mnemonic: "",
			privateKey: "",
			hasAgreed: false
        }
    },
    computed: {
		passwordMessage() {
			return (this.passwordInvalid) ? "The password is incorrect" : ""
        },
        showRecoveryPhrase() {
			return Array.isArray(this.mnemonic) && this.mnemonic[0] !== ''
        }
    },
	methods: {
		nextStep() {
			this.state = this.state += 1
			if(this.state >= 3){
				this.close()
				this.state = 0
			}
		},
		handleCheckPassword() {
			this.checkPassword({password:this.password})
			.then((res) => {
				this.mnemonic = res.mnemonic.split(' ')
				this.privateKey = res.privateKey
				this.nextStep()
			})
			.catch(() => {
				this.passwordInvalid = true
			})
		}
	},
	watch: {
		password() {
			if(this.passwordInvalid){
				this.passwordInvalid = false
			}
		}
	}
};
</script>

<template>
	<Popup :closeable="true" @onClose="close" :blur="false" class="recovery-popup" :isOpen="true">
		<div v-if="this.state === 0">
			<div class="popup-header">Recovery</div>
			<div class="popup-description">Enter your password and continue to the next page to be able to see the recovery information.</div>
			<div class="password-container">
				<InputBox 
				v-model="password"
				inputType="password"
				placeholder="Wallet password"
				:showMessage="true"
				:borderColor="colors.btfsBlue500"
				:passwordPlaceholderColor="colors.gray500"
				:iconColor="colors.gray500"
				:isInvalid="passwordInvalid"
				:message="passwordMessage"
				/>
			</div>
			<div class="button-container" >
				<Button :cb="handleCheckPassword" :isDisabled="passwordInvalid">Continue</Button>
			</div>
		</div>
		<div v-if="this.state === 1">
			<div class="popup-header">Important message</div>
			<div class="popup-description popup-description-1">The recovery information is unique and only you should have access. We encourage you to write these words down and keep them in a safe place. Avoid saving it in a computer or online service, and do not take a screenshot of it. If you lose it or share it with others, you may lose your assets.</div>
			<div class="text-sm">
				Looking for more information? Read our <a href="https://www.bittorrent.com/token/bittorrent-file-system/faq/" target="_blank" rel="noreferrer noopener">FAQs</a>
			</div>
			<div class="text-sm tos-container">
				<bt-checkbox @change="(val) => {hasAgreed = val}"> 
					<template v-slot:right>
						I agree to the terms and conditions of BTFS.
					</template>
				</bt-checkbox>
			</div>
			<div class="button-container">
				<Button :cb="nextStep" :isDisabled="!hasAgreed">Continue</Button>
			</div>
		</div>
		<div v-if="this.state === 2">
			<div class="subtitle">
				Recovery phrase
			</div>
			<div class="recovery-phrase-container">
				<div 
				v-if="!showRecoveryPhrase"
				class="light-container"
				>
					<p>Not available</p>
					<br/>
					<a href="https://docs.btfs.io/docs/faq" target="_blank">Click here to learn more</a>
				</div>
				<div
				v-if="showRecoveryPhrase"
				>
					<div class="recovery-right-container">
						<div class="recovery-answer-key-container" >
							<div v-for="(phrase, index) in mnemonic"
								class="recovery-answer-key"
								:key="index">{{phrase}}
							</div>
						</div>
					</div>
					<div class="button-container copy-recovery-phrase">
						<Button type="copy" :copyValue="mnemonic.join(' ')">Copy recovery phrase to clipboard</Button>
					</div>
				</div>
			</div>
			<div class="subtitle">
				Private Key
			</div>
			<div class="private-key-container">
				<div class="light-container">
					{{privateKey}}
				</div>
				<div class="button-container copy-private-key">
					<Button type="copy" :copyValue="privateKey">Copy private key to clipboard</Button>
				</div>
			</div>
		</div>
	</Popup>
</template>

