<script>
import Button from '../components/Button.vue'
import Notification from '../components/Notification.vue'

export default {
	name: 'Dashboard',
	props: {
		version: {
			type: String,
			required: true
		},
		id: {
			type: String,
			required: true
		},
		notificationState: {
			type: String,
			default: ''
		},
		notificationClose: {
			type: Function,
			default: () => {}
		},
		hostPricing: {
			type: Number,
		},
		hostSuggestedPricing: {
			type: Number,
		},
		storageAnnouncePrice: {
			type: Function,
			default: () => {}
		},
	},
	components: {
		Button,
		Notification
	},
	methods: {
		handleNavClick(navItem) {
			this.selectedNavItem = navItem
		},
		toggleAdvanced() {
			this.showAdvanced = !this.showAdvanced
		},
		notificationDone() {
			return this.$store.dispatch('notificationComplete', 'settings')
		},
		handlePriceUpdate() {

			this.storageAnnouncePrice({hostStoragePrice : this.newHostPricing})
			.then(() => {
				this.toggleAdvanced()
				return this.notificationDone()
			})
		}
	},
	data() {
		return {
			navList: [
				'host',
			],
			selectedNavItem: 'host',
			showCopyTooltip: false,
			showAdvanced: false,
			newHostPricing: this.hostPricing
		}
	},
	computed: {
		showNotification() {
			return this.notificationState !== ''
		},
		equalSuggestedPricing() {
			return this.hostPricing === this.hostSuggestedPricing
		},
		isPricingValid() {
			return this.newHostPricing > 0
		}
	},
	mounted() {
	},
	beforeDestroy() {
	}
}
</script>

<template>
	<div class="settings">
		<p class="title">Settings</p>
		<div class="settings-nav">
			<div
				v-for="navItem in navList" 
				:key="navItem"
				class="settings-nav-item"
				:class="{'settings-nav-item-selected': navItem === selectedNavItem}"
				@click="()=> {handleNavClick(navItem)}"
			>
				<!-- {{ navItem }} -->
			</div>
		</div>
		<div 
			v-if="selectedNavItem === 'host'"
			class="settings-host"
		>
			<div class="subtitle-container">
				<p class="subtitle">
					current version
				</p>
			</div>
			<p class="content">
				{{ version }}
			</p>
			<div class="subtitle-container">
				<p class="subtitle">
					host node id
				</p>
				<div>
					<Button type="copy" :copyValue="id"> 
							Copy 
					</Button>
				</div>
			</div>
			<div class="content node-id">
				<p ref="nodeId" id="node-id">
					{{ id }}
				</p>
			</div>

			<div class="subtitle-container">
				<p class="subtitle">
					Pricing tb/month
				</p>
				<div>
					<Button type="outline" :isDisabled="showAdvanced" :cb="toggleAdvanced"> 
						<div 
						class="button" 
						v-tooltip.bottom="{ 
							content: 'Edit monthly host pricing', 
							classes: ['tooltip'], 
						}">
							Edit 
							<svg-icon icon="edit" :hasFill="true" class="icon"/>
						</div>
					</Button>
				</div>
			</div>
			<div class="content">
				{{hostPricing.toLocaleString()}} BTT
				<p 
				v-if="!equalSuggestedPricing && !showAdvanced"
				class="assistive-text">
					Suggested price: {{this.hostSuggestedPricing.toLocaleString()}} BTT
				</p>

				<div v-if="showAdvanced">
					<div class="pricing">
						<div class="">
							Edit Pricing
						</div>
						<div class="pricing-input">
							<input v-model="newHostPricing" type="number" :class="{invalid: !isPricingValid}"/> <span class="subtitle">BTT</span>
						</div>
					</div>
					<p v-if="!isPricingValid" class="assistive-text invalid">Please enter a valid amount</p>
					<p v-if="isPricingValid" class="assistive-text">Suggested price: {{this.hostSuggestedPricing.toLocaleString()}} BTT</p>
					<div class="pricing-description">
						Changes to advanced settings can impact your host earnings. Please make sure you understand the effects before making any changes.
						<br/><br/>
						Learn more on our <a href="https://www.bittorrent.com/token/bittorrent-file-system/faq/" target="_blank" rel="noreferrer noopener">FAQs page</a>. 
					</div>
					<div class="button-container">
						<Button :cb="toggleAdvanced" type="outline" class="cancel">Cancel</Button>
						<Button :cb="handlePriceUpdate" :isDisabled="!isPricingValid">Save Changes</Button>
					</div>
				</div>
			</div>

		</div>
		<div
			v-if="selectedNavItem === 'wallet'"
			class="settings-wallet"
		>

		</div>
		<Notification v-if="showNotification" :state="notificationState" :handleClose="notificationClose">
			<template #complete>
			Pricing updated
			</template>
		</Notification>
	</div>
</template>

